ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  position: relative;
  border-radius: 10px;
  text-align: center;
  margin: 45px 25%;
  padding: 60px 10px;
  background-color: rgba(244, 244, 244, 0.1);
}

@media only screen and (max-width: 1100px) {
  .container {
    margin: 45px 10%;
  }
}
