.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 5px;
  margin: 20px;
  list-style: none;
  box-shadow: -2px 3px 3px 0px #4c248b;
}

.textContainer {
  display: flex;
  text-align: start;
}

.textTaskCompleted {
  text-decoration: line-through;
}
